var state = require('../lib/state-machine');

class FractalOfLife {

    constructor(emerga) {
        this.emerga = emerga;
    }

    init() {
        var iterop = this.fractalOfLifeBloom;
        var machine = this.stateMachine = new state.StateMachine();
        machine.builders.add(new state.StateBuilder(machine, null, (el, iter) => {
            machine.elements.add(new state.EntityElement(
                machine, { x: 0, y: 0 },
                iterop));
        }));
        machine.operators.add(new state.StateOperator(machine, null, (el, iter) => {
            // execute the operator on the element
            // and add resulting elements to list
            el.execute(iter);
        }));
    }

    fractalOfLifeBloom(el, iter) {
        console.log(iter);
    }

    run() {
        this.stateMachine.run(5);
    }

}
module.exports = FractalOfLife;